<template>
  <div class="wrapper">
   <Navbar
    @setSearchVal="searchValue = $event"
   />
   <Notes 
    @delNote="delNote"
    @change="change" 
    :notes="filterNotes"
   />
   <Modal 
      @closeModal="isModalOpen = false"
      @addNote="addNote"
      @editNote="editNote"
      v-show="isModalOpen"
      :edit="edit"
      :editedNote="editedNote"
    />
   <button 
      class="add__note" 
      @click="isModalOpen = true, edit = false" 
      v-if="!isModalOpen"
   >
    <img src="@/assets/images/edit.svg" alt="">
   </button>
  </div>
</template>

<script>
import Modal from './components/Modal.vue';
import Navbar from './components/Navbar.vue';
import Notes from './components/Notes.vue';
import { v4 as uuidv4 } from 'uuid';

  export default {
    components: {
    Navbar,
    Notes,
    Modal
},
    data() {
      return {
        isModalOpen: false,
        notes: [],
        edit: false,
        editedNote: null,
        searchValue: ''
      }
    },
    computed: {
      filterNotes() {
        return this.searchValue ? this.notes.filter(note => note.name.toLowerCase().includes(this.searchValue.toLowerCase())) : this.notes
      }
    },
    mounted() {
      this.getNotes()
    },
    methods: {
      addNote(note) {
        note.id = uuidv4()
        this.notes.push(note)
      },
      delNote(id) {
        let idx = this.notes.findIndex((note) => note.id == id)
        this.notes.splice(idx, 1)
      },
      change(id) {
        this.isModalOpen = this.edit = true
        let currentNote = this.notes.find(note => note.id == id)
        this.editedNote = currentNote
      },
      editNote(changedNote) {
        this.notes.forEach(note => {
          if(note.id == changedNote.id) {
            note.name = changedNote.name
            note.text = changedNote.text
            note.date = changedNote.date
          }
        })
      },
      getNotes() {
        const localNotes = localStorage.notes
        if(localNotes) {
          this.notes = JSON.parse(localNotes)
        }
      }
    },
    watch: {
      notes: {
        handler(newVal) {
          localStorage.notes = JSON.stringify(this.notes)
        },
        deep: true
      }
    }
}
</script>

